import React from 'react';
import PropTypes from 'prop-types';
import './ghostbutton.css';

export const GhostButton = ({ Default, backgroundColor, size, label, ...props }) => {
  const mode = Default ? 'storybook-ghostbutton--Default' : 'storybook-ghostbutton--secondary';
  return (
    <button
      type="ghostbutton"
      className={['storybook-ghostbutton', `storybook-ghostbutton--${size}`, mode].join(' ')}
      style={backgroundColor && { backgroundColor }}
      {...props}
    >
      {label}
    </button>
  );
};

GhostButton.propTypes = {

  Default: PropTypes.bool,

  backgroundColor: PropTypes.string,

  size: PropTypes.oneOf(['small', 'medium', 'large', 'larger', 'max-large']),

  label: PropTypes.string.isRequired,
};

GhostButton.defaultProps = {
  backgroundColor: null,
  Default: false,
  size: 'medium',
};
